<template>
  <div id="app">
    <div class="header">
      <div>
        <img class="logo" src="./assets/logo.jpg">
      </div>
      <div class="title">
          <span class="title1">平安保险方案专家</span>
          <p>
              <span class="title2">一站式为您和你家人免费咨询&nbsp;|</span>
              <span class="title2 wire">不限社保用药</span>
          </p>
      </div>
      <div class="consult" v-show="subscribe">
          <p class="consult-test">免费咨询通道</p>
          <div class="triangle"></div>
      </div>
  </div>
  <div v-show="subscribe" class="container">
      <p class="title3">已有<span>{{number}}</span>报名索要分析</p>
      <div>
          <input type="text" v-model="name" class="int" placeholder="请输入您的姓名">
          <!-- <alert type="warning" show-icon>
          <template slot="desc">
            请输入您的姓名
          </template>
    </alert> -->
      </div>
      <div>
          <input type="text" name="phone"  v-model="phone" class="int" placeholder="请输入您的手机号">
      </div>
      <div>
          <input type="text" class="auth" v-model="code" placeholder="请输入验证码">
          <button type="button" class="auth-span" :class="{ bg:disabled }" :disabled="disabled" @click="sendcode">{{btntxt}}</button>
      </div>
      <div>
          <button id="btn" class="btn" @click="submit">免费预约专家分析</button>
      </div>
      <p class="hint">温馨提示：预约后，平安健康专家将通过电话与你联系，确认您的个人信息情况后会免费定制最佳方案！定制方案绝不收费！</p>
  </div>
  <div v-show="!subscribe" >
      <div class="yes">
        <img class="yesimg" src="./assets/icon.png">
        <p class="yestitle1">预约成功！</p>
        <p class="yestitle2">方案专家将在24小时内来电，敬请注意接听喔。</p>
      </div>
      <p class="hint">温馨提示：预约后，平安健康专家将通过电话与你联系，确认您的个人信息情况后会免费定制最佳方案！定制方案绝不收费！</p>
      <button class="btn" @click="showto">返回</button>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      number: 63898,
      time: 0,
      btntxt:"获取验证码",
      subscribe: true,
      disabled:false,
      phone:'',
      name:'',
      code:''
    }
  },
  methods: {
    // 验证手机号码
    blurbtn() {
      if(this.name == ''){
        this.$Message.warning('请输入手机号码');
      }
      
    },
    sendcode() {
      var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
       if(this.phone==''){
             // alert("请输入手机号码");
             this.$Message.warning('请输入手机号码');
          }else if(!reg.test(this.phone)){
              this.$Message.warning('手机格式不正确');
          }else{
              this.time=60;
              this.disabled=true;
              this.timer();
         }
     },
     // 重新获取验证码
     timer() {
          if (this.time > 0) {
               this.time--;
               this.btntxt=this.time+"s后重新获取";
               setTimeout(this.timer, 1000);
          } else{
               this.time=0;
               this.btntxt="获取验证码";
               this.disabled=false;
          }
      },
      // 提交信息
     submit() {
      if(this.name == ''){
         this.$Message.warning('请输入姓名');
      }else if(this.phone=='') {
         this.$Message.warning('请输入手机号码');
      }else if(this.code=='') {
         this.$Message.warning('请输入验证码');
      }else {
        this.succeed();
      }
     },
     // 预约成功
     succeed() {
      this.subscribe = !this.subscribe
        if(this.subscribe) {
          this.subscribe = true
        }else {
           this.subscribe = false
        }
     },
     // 返回
     showto() {
       this.subscribe = true
     },
  }
}
</script>

<style scoped>
.header {
    width: 100%;
}

.logo {
    width: 100%;
    height: 100%;
}

.title {
    padding-left: 0.75rem;
}
.title1 {
  display: block;
  padding: 0.5rem 0rem;
    font-size: 1.1rem;
    color: black;
    font-weight: 800;
}

.title2 {
    display: inline-block;
    vertical-align: top;
    font-size: 0.8rem;
    color: #A3A3A3;
    font-weight: bold;
}
.consult {
    width: 100%;
    height: 3rem;
    background-color: #A3A3A3;
    border: 0px solid;
    border-radius: 0.4rem;
}

.consult-test {
    position: relative;
    text-align: center;
    color: #fff;
    font-size: 1.1rem;
    padding-top: 0.4rem;
}

.triangle {
    width: 0;
    height: 0;
    border: 0.65rem solid transparent;
    border-top: 0.65rem solid #fff;
    margin-left: 47%;
}

.container {
  
    width: 80%;
    margin: 0 auto;
}

.title3 {
    text-align: center;
    font-size: 1.2rem;
    margin: 0.3rem 0;
    color: #A3A3A3;
}

.title3 span {
    display: inline-block;
    padding: 0.5rem 0.5rem;
    color: red;
}

.int {
    width: 100%;
    height: 2.5rem;
    border: 1px solid #A3A3A3;
    border-radius: 0.5rem;
    margin-bottom: 0.35rem;
    font-size: 0.85rem;
    outline: none;
    text-indent: 0.6rem;
}

.auth {
    width: 41%;
    height: 2.5rem;
    border: 1px solid #A3A3A3;
    border-radius: 0.5rem;
    margin-bottom: 0.35rem;
    font-size: 0.85rem;
    outline: none;
    text-indent: 0.6rem;
}

.auth-span {
    display: inline-block;
    vertical-align: top;
    width: 46%;
    height: 2.5rem;
    background-color: #A9D18F;
    border-radius: 0.9rem;
    text-align: center;
    font-size: 0.75rem;
    margin-left: 1.1rem;
    line-height: 2.45rem;
    color: #fff;
    border-width: 0rem;
    outline: none;
}

.btn {
    width: 100%;
    height: 2.6rem;
    text-align: center;
    background-color: red;
    border-radius: 0.75rem;
    color: #fff;
    line-height: 2.5rem;
    font-size: 1.0rem;
    border-width: 0rem;
    outline: none;
}
.bg {
  background-color: #D1D5D3;
}

.hint {
    font-size: 0.6rem;
    color: #A3A3A3;
    padding: 0.5rem 1.25rem
}

.yes {
  text-align: center;
  margin-top: 3rem;
}
.yestitle1 {
  display: block;
  font-weight: 800;
  font-size: 1.5rem;
  padding-bottom: 0.8rem;
}
.yestitle2 {
  padding-bottom: 2rem;
}
</style>


